const productsContainer = document.querySelector(".products-container");

const jsonData = {
  data: [
    {
      name: "Cosmetics",
      productList: [
        {
          name: "Hair Oil",
          price: 122
        },
        {
          name: "Face wash",
          price: 123
        }
      ]
    },
    {
      name: "Household",
      productList: [
        {
          name: "Hair Oil",
          price: 122
        },
        {
          name: "Face wash",
          price: 123
        }
      ]
    }
  ]
};

function CosmeticsProduct() {
  const data = jsonData.data;

  const productsList = data.forEach((product) => {
    const nameOfProductsType = product.name;
    const listsItemContainer = document.createElement("ul");
    listsItemContainer.innerHTML = `<strong class="product-type">${product.name}-</strong>`;
    const productListItem = product.productList;
    // console.log(productListItem);
    const ListItem = productListItem.forEach((item) => {
      const listOfItems = document.createElement("li");
      listOfItems.innerHTML = `<strong>${item.name}</strong> : ${item.price}`;
      listsItemContainer.appendChild(listOfItems);
    });
    productsContainer.appendChild(listsItemContainer);
  });
}

CosmeticsProduct();
